import Vue from 'vue'
import Router from 'vue-router'
import HelloContainer from "./components/HelloWorld.vue"


//1.引入Test组件
// import Test from "./components/Test.vue"
import Home from "./components/Home.vue"
import Login from "./components/Login.vue"
import Register from "./components/Register.vue"
import GoodList from "./components/Home/GoodList.vue"
import GoodInfo from "./components/Home/GoodInfo.vue"
import ShopCart from "./components/Home/ShopCart.vue"
import Me from "./components/Home/Me.vue"
import Merorder from "./components/Home/Merorder.vue"
import Adress from "./components/Home/Adress.vue"
import Update from "./components/Home/Update.vue"
import Insert from "./components/Home/Insert.vue"

Vue.use(Router)
//2.为Test组件配置访问路径 /Test
export default new Router({
  routes: [
    // {path:"/Insert",component:Insert},
    // {path:"/Update",component:Update},
    // {path:"/Adress",component:Adress},
    // {path:"/Merorder",component:Merorder},
    // {path:"/GoodInfo",component:GoodInfo},
    {path:"/Register",component:Register},
    {path:"/Login",component:Login},
    // {path:"/Home",component:Home},
    // {path:"/GoodList",component:GoodList},
    // {path:"/Me",component:Me},
    // {path:"/ShopCart",component:ShopCart},
    {
      path:"/",
      component:HelloContainer,
      children:[
        {
          path:"/",
          component:Home,
        },
        {
          path:"/Home",
          component:Home,
        },
        {
          path:"/GoodList",
          component:GoodList
        },
        {
          path:"/Me",
          component:Me,
        },
        {path:"/Merorder",component:Merorder},
        {
          path:"/Adress",
          component:Adress,
        },
        {path:"/Insert",component:Insert},
        {path:"/Update",component:Update},
        
      ]
    },
    {path:"/GoodInfo",component:GoodInfo},
    {path:"/ShopCart",component:ShopCart},

  ]
})




