<template>
  <div class="app-goodlist">
    <!-- 商品搜索框 -->
    <div class="search">
      <input type="text" placeholder="请输入您想要的商品" v-model="sousuo">
      <mt-button class="btn" @click="cha">搜索</mt-button>
    </div>
    <!-- 商品详细信息 start -->
    <div class="goods">
      <div class="goods-item" v-for="item of list">
        <!-- GoodList.vue -->
        <img :src="item.img" @click.prevent="jumpInfo" :data-lid=item.id>
        <div class="title">
          <span>{{item.title}}</span>
          </div>
        <div class="info">
          <span class="now">{{item.price}}</span>
        </div>
        <div class="sell">
          <span>{{item.comment}}</span>
          <span>{{item.good}}</span>
        </div>
        <!-- 商品详细信息 end -->
      </div>
    </div> 
    <!-- 加载更多 -->
    <mt-button class="aa" size="large" type="primary" @click="loadMore">加载更多</mt-button>
    <!-- 底部导航 -->
    <mt-tabbar fixed>
      <router-link to='/'>
        <mt-tab-item id="tab1" class="shouye"  >
          <img slot="icon" src="../../img/tabs/index.png">
          首页
        </mt-tab-item>
       </router-link>
        <!-- <router-link to='/'> -->
        <mt-tab-item id="tab2" class="haha">
          <img slot="icon" src="../../img/tabs/product-active.png">
          全部商品
        </mt-tab-item>
         <!-- </router-link> -->
      <router-link to="/ShopCart">
        <mt-tab-item id="tab3"  class="shop">
          <img slot="icon" src="../../img/tabs/shop.png">
          购物车
        </mt-tab-item>
      </router-link>
      <router-link to="/Me">
        <mt-tab-item id="tab4"  class="me">
          <img slot="icon" src="../../img/me.png">
          个人中心
        </mt-tab-item>
      </router-link>
    </mt-tabbar>
  </div>
</template>
<script>
import {Toast} from "mint-ui";
export default {
  data(){ 
    return{
      list:[],  //模板中显示商品列表
      pno:0,    //第几页   初始值0
      pageSize:6,
      btn:"",  //判断列表是否有商品
      sousuo:""
    }
  },
  created(){
    //当组件创建成功后立即调用加载下一页方法
    this.loadMore();
  },
  methods:{
    //跳转商品详情组件
    jumpInfo(e){
      var lid=e.target.dataset.lid;
      //console.log(lid);
      this.$router.push("/GoodInfo?lid="+lid);
    },
    //加载下一页数据并且将数据显示在模板上
    loadMore(){
      this.pno++;
      // console.log(this.pno)
      //1.创建变量url请求地址
      var url= "http://127.0.0.1:3000";
      url+="/getProducts?pno="+this.pno+"&pageSize="+this.pageSize;
      //console.log(this.pageSize);
      //2.发送ajax请求
      this.axios.get(url).then(result=>{
        //3.获取服务器返回结果保存data
        //3.获取服务器返回结果 追加list
        //使用concat 拼接数组
        // var rows = result.data;
        for(var item of result.data){
          this.list.push( item);
        }
        this.pageSize=4;
      })
    },
    cha(){
      this.list=[];
      var url="search?sousuo="   +this.sousuo;
      this.axios.get(url).then(result=>{
         console.log(result.data.data)
        var arr=result.data.data;
        if(arr.length==0){
          Toast("很抱歉,您搜索的未找到")
        }else{
          this.list=arr;
          // console.log(this.list);
        } 
      })
    }
  },
}
</script>
<style>
/*搜索栏*/
.app-goodlist .search{
  width: 100%;
  left:0;
  background-color:#99c2c7;
  position:fixed;
}
.app-goodlist .search input{
  width:70%;
  height:30px;
  margin-top:5px;
  margin-left:20px;
  margin-right:10px;
  margin-bottom:0;
  border-radius:100px;
}
.app-goodlist .search .btn{
  width:60px;
  height:30px;
  margin-top:5px;
  margin-bottom:5px;
}

/*外层父元素*/
.app-goodlist .goods{
  padding-top:40px !important;
  display:flex; /*弹性布局*/
  flex-wrap: wrap; /*子元素换行*/
  justify-content: space-between; /*两端对齐*/
  padding:4px;
}
/*商品信息*/
.app-goodlist .goods-item{
  width:49%; /*商品信息元素宽度*/
  border:1px solid #ccc; /*边框*/
  margin:2px 0;  /* 外补丁 */
  padding:2px;
  display:flex;/* 子元素弹性布局 */
  flex-direction:column; /* 排列方式:按列 */
  min-height:245px; /*最小高度*/
}
/*商品图片*/
.app-goodlist .goods-item img{
  width:100%;
}
/*商品名称*/
.title>span{
  height:36px;
  font-size: 13px;
  color:#333;
  letter-spacing: 0;
  line-height: 18px;
  word-break: break-all;
}
.title{
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
}
.now{
  color:#e93b3d;
  font-size:13px;
}
.sell{
  font-size:12px;
  color:#999;
  line-height: 18px;
  min-height: 18px;
}
.sell :last-child{
  margin-left:15px;
}
.aa{
  margin-bottom:58px;
}
.mint-button--primary{
  background-color:#99c2c7 !important;
}
/*底部导航*/
.shouye{
  position: relative;
  left:40px;
}
.haha{
  position: relative;
  left:-20px !important;
}
.shop{
  position: relative;
  left:-75px;
}
.me{
  position: relative;
  left:-30px;
}
</style>





