<template>  
  <div class="app-login">
    <div id="title">
    <img src="../img/icon.png">
    <h1>欢迎登录</h1>
    </div>
    <input type="text" name="uname" v-model="uname" placeholder="请输入用户名" class="inp"><br>
    <input type="password" name="upwd" v-model="upwd" placeholder="请输入密码" class="inp"><br>
    <mt-button @click="btnClick" size="large" type="primary">登录</mt-button>
  </div>
</template>
<script>
//1.引入mint-ui Toast局部使用
import {Toast} from "mint-ui"
export default {
  data(){
    return {
      uname:"",
      upwd:""
    }
  },
  methods:{
    btnClick(){
      //1.获取用户输入用户名和密码
      var uname=this.uname;
      var upwd=this.upwd;
      var reg=/^[a-z0-9_]{1,8}$/i;
      if(!reg.test(uname)){
        Toast("用户名格式不正确,请检查")
        return;
      }
      if(!reg.test(upwd)){
        Toast("密码格式不正确,请检查")
        return;
      }
      var postData=this.qs.stringify({
        uname:uname,
        upwd:upwd
      })
      //console.log(postData);
      //3.发送ajax请求
      var url="login";
      this.axios.post(url,postData).then(result=>{
        if(result.data.code==1){
          Toast("登录成功");
          this.$router.push("/Home");
        } 
      });
    }
  }
}
</script>
<style scoped>
#title{
    text-align: center;
    margin-bottom: 30px;
}
.inp{
    border:none;
    background: #efeff4;
    border-bottom: 1px solid #aaa9a9;
}
</style>



