<template>
  <div class="app-register">
    <div id="title">
    <img src="../img/icon.png">
    <h1>欢迎注册</h1>
    </div>
    <input type="text" name="uname" v-model="uname" placeholder="请输入用户名" class="inp"><br>
    <input type="password" name="upwd" v-model="upwd" placeholder="请输入密码" class="inp"><br>
    <!-- <input type="text" name="phone" v-model="phone" placeholder="请输入您的手机号" class="inp"> -->
    <mt-button @click="btnClick" size="large" type="primary">注册</mt-button>
  </div>
</template>
<script>
import {Toast} from "mint-ui"
export default {
  data(){
    return {
      uname:"",
      upwd:""
      // phone:''
    }
  },
  methods:{
    btnClick(){
      var uname=this.uname;
      var upwd=this.upwd;
      var reg1=/^[a-z0-9_]{1,8}$/i;
      if(!reg1.test(uname)){
        Toast("用户名格式不正确,请检查")
        return;
      }
      var reg2=/^.{6,15}$/;
      if(!reg2.test(upwd)){
        Toast("密码格式不正确,请检查")
        return;
      }
      // var reg3=/^((\+86|0086)\s+)?1[3-8]\d{9}$/;
      // if(!reg3.test(phone)){
      //   Toast("您的手机格式不正确");
      //   return;
      // }
      var postData=this.qs.stringify({
        uname:uname,
        upwd:upwd,
        // phone:phone
      })
      //发送请求
      var url="register";
      this.axios.post(url,postData).then(result=>{
        Toast("注册成功");
        this.$router.push("/login");
      })
    }
  }
}
</script>
<style scoped>
#title{
  text-align: center;
  margin-bottom: 30px;
}
.inp{
  border:none;
  background: #efeff4;
  border-bottom: 1px solid #aaa9a9;
}
.mint-button--large{
  background: #bed5ab;
  color: #fff;
}
</style>



