<script>
import {Toast} from "mint-ui"
export default {
	data(){
		return{
			lid:this.$route.query.lid,
			list:[], //轮播列表
			info:{}, //获取标题价格
			pic:{}   //对应商品的详情图片
		}
	},
	created(){
		this.handleImage();   //轮播
		this.handleDetails(); //详情页标题
		this.handlePic();     //详情图片
	},
	methods:{
		// 点击按钮添加购物车
		getShopcart(){
			var pid=this.lid;
			var price=this.info[0].price;
			console.log(this.info)
			var pname=this.info[0].title;
			var pic=this.info[0].pic;
			var url="getShopcart?pid="+pid+"&price="+price+"&pname="+pname+"&pic="+pic;
			//获取返回结果
			this.axios(url).then(result=>{
				console.log(result);
				this.$store.commit("increment");
			});
		},
		handlePic(){
			//console.log("123");
      var url = "pic?lid="+this.lid;
      this.axios.get(url).then(result => {
				this.pic = result.data.data;
				//console.log(this.pic)
      });
		},
		handleDetails(){
      //console.log("123");
      var url = "details?lid="+this.lid;
      this.axios.get(url).then(result => {
				this.info = result.data.data;
				// console.log(this.info)
      });
    },
		//加载轮播图数据
		handleImage() {
      //console.log("123");
      var url = "imglist?lid="+this.lid;
      this.axios.get(url).then(result => {
				this.list = result.data;
				//console.log(this.list)
      });
    }
	}
}
</script>
