<template>
  <div class="app-shopcart">
    <!-- 购物车标题 -->
    <mt-header fixed title="购物车">
      <router-link to="/GoodInfo" slot="left">
        <mt-button icon="back"></mt-button>
      </router-link>
      <mt-button icon="more" slot="right"></mt-button>
    </mt-header>
    <!-- 定单地址 -->
    <div class="mui-card">
      <div class="mui-card-content">
        <div class="mui-card-content-inner">
          <div class="mui-card-content-inner">
            <div class="dingdan">收货地址 :</div>
              <p>
                <span>收货人 : {{this.addrlist.name}}  </span>
              </p>
              <p>联系方式 : {{addrlist.tle}}</p>
              <p>地址: {{addrlist.place}}</p>
            </div>
        </div>
      </div>
    </div>
    <!-- 购物车列表 -->
    <!-- 购物车导航 -->
    <!-- 商品信息开始 -->
    <ul class="mui-table-view">
      <li class="mui-table-view-cell mui-media" 
      v-for="(item,i) of shoplist" :key=i>
        <!-- 商品复选框 -->
        <input type="checkbox" class="check" :checked="item.cb" :data-id="item.id" :data-i="i" @click="modifyItem">
          <img class="mui-media-object mui-pull-left" :src="item.pic">
          <div class="mui-media-body">
            {{item.pname}}
            <p class='mui-ellipsis'>
              ¥{{item.price}}
              <input type="button" class="del" value="删除" @click="removeItem" :data-id="item.id" :data-idx="i"/>
              </p>    
          </div> 
      </li>
    </ul>
    <!-- 商品信息结束 -->
    <div class="footer">
      <div class="quan">
        <!-- 全选按钮 -->
        <input type="checkbox" class="qx" @click="selectAll" :checked="allcb">
        <p class="xx">
          <span>全选</span>
          <!-- 删除选中商品 -->
          <input type="button" class="btn" value="删除选中商品" @click="delItem">
        </p>
      </div>
      <div class="zong">
        <p>
          <span>总计:</span>
          <span>{{total.toFixed(2)}}</span>
        </p>
        <p>
          <span>总额 {{total.toFixed(2)}}立减0.00</span>  
        </p>
      </div>
      <div class="jie" @click="mes">去结算</div>
    </div>  
  </div>
</template>
<script>
import {Toast} from "mint-ui";
//弹出框
import {MessageBox} from 'mint-ui'
export default {
  data(){
    return{
      addrlist:[], //订单地址
      // id:1,  //商品列表
      shoplist:[],
      uid:1,
      allcb:false
    }
  },
  created(){
    this.addr();
    this.loadMore();
  },
  //计算总价
  computed:{
    total(){
      var sum=0;
      for(var p of this.shoplist){
        if(this.allcb){
        sum+=p.price;
      }
    }
    return sum;
    }
  },
  methods:{
    //结算成功
    mes(){
      if(this.allcb){
        MessageBox('下单成功', '自动跳转订单页');
        this.shoplist='';
        this.$router.push("/Merorder");
      }else{
        MessageBox('提示', '请选择您想要的商品');
      }
    },
    //全选按钮
    selectAll(e){
      //1.获取全选按钮选中状态
      var rs=e.target.checked;
      //2.遍历数据所有元素
      for(var item of this.shoplist){
        item.cb= rs;
      }
      //3.item.cb=全选状态
      //4.修改全选数据状态
      this.allcb=rs;  
    },
    //修改复选框的状态 选中 取消
    modifyItem(e){
      //1.获得当前元素选中状态 true false
      var rs=e.target.checked;
      //2.当前元素在数组中下标
      var i=e.target.dataset.i;
      //3.将状态赋值当前数组下标中对象cb值
      this.shoplist[i].cb=rs;
    },
    //删除选中多个商品
    delItem(){
      //[{cb:true,id:1},{cb:true,id:3}] "1,3"
      var html="";
      for(var item of this.shoplist){
        if(item.cb){
          html+=item.id+",";
        }
      }
      if(html.length==0){
        Toast("请选择您需要删除商品!");
        return;
      }
      //截取字符串去除最后, 1,6,7
      html=html.substring(0,html.length-1);
      console.log(html);
      //发送ajax ids=1,6,7
      var url="removeMItem?ids="+html;
      this.axios.get(url).then(result=>{
        if(result.data.code==1){
          Toast("删除成功");
          this.loadMore();
        }else{
          Toast("删除失败");
        }
      })
    },
    //删除购物车中的某个商品
    removeItem(e){
      //1.获取购物车商品id
      var id=e.target.dataset.id;
      var idx=e.target.dataset.idx;
      //2.创建url
      var url="http://127.0.0.1:3000";
      url+="/removeItem";
      var postData=this.qs.stringify({
        id:id
      });
      //3.发送ajax请求
      this.axios.post(url,postData).then(result=>{
        console.log(result);
        if(result.data.code==1){
          //删除对应数据中值
          //1:获取下标
          //2:依据下标删除指定数组商品
          //删除数组中对应下标商品
          //splice(数组下表,个数)
          this.shoplist.splice(idx,1);
          Toast("删除成功");
        }else{
          Toast("删除失败");
        }
      })
    },
    //加载当前用户购物车列表
    loadMore(){
      var url="cart";
      this.axios.get(url).then(result=>{
        //console.log(result.data)
        this.shoplist=result.data.data;
        //新添加功能:复选框状态
        var rows=result.data.data;
        //为数组中每个对象添加cb初始值false
        for(var item of rows){
          item.cb=false;
        }
        //将数据保存到shoplist中
        this.shoplist=rows;
        console.log(this.shoplist);
      })
    },
    addr(){
      var url="addr";
      this.axios.get(url).then(result=>{
        //console.log(result.data);
        this.addrlist=result.data.data[0];
      })
    }
  }
  
}
</script>
<style>
/* 地址样式 */
.mui-card{
  box-shadow: 0px 1px 10px 1px #d2d1d3; 
  margin-top:15px !important;
  
  top:35px;
  height: 112px;
  width:345px;
  margin:15px;
}
p{
  margin-left: 20px;
}
/*购物车列表*/
.mui-table-view{
  margin-top:50px !important;
}
.mui-table-view .mui-media-object{
  max-width:75px  !important;
  height:75px !important;
  position: relative;
  left: 17px;
  top: 5px;
}
.mui-table-view .mui-media-body{
  font-size:13px;
  position: relative;
  left: 15px;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}
.mui-table-view-cell p{
  line-height:16px;
  color:#e93b3d;
  font-size:16px;
  margin-left: 5px;
  margin-top: 5px;
}
input[type='checkbox']{
  position: relative;
  left: -86px;
  top: 28px;
}
.del{
  position: relative;
  left:30px;
  height: 30px;
  width:70px;
  padding:0 !important;
  height:20px;
  width:50px;
  margin-left:110px;
}
/*底部*/
.footer{
  display:flex;
  height:60px;
  background-color:#fff;
  margin-top:10px;
  position: fixed;
  bottom:0;
}
.footer .quan{
  width:100px;
  margin-left:3%;
  margin-right:15px;
}
.footer .quan .p{
  margin-left:5px;
}
.footer .quan .xx span{
  position: relative;
  top:16px;
  left:20px;
}
.footer .quan .qx{
  position: relative;
  left:5%;
  top:35px;
}
.footer .quan .btn{
  height:25px;
  width:90px;
  position:relative;
  top:-40px !important;
  line-height: 25px;
  padding:0;
  margin-top:12px !important;
} 
.footer .zong{
  margin-top: 5px;
  width:130px;
  /* margin-left: 35px; */
  line-height: 15px;
}
.footer .zong p{
  margin:0;
  color:#000;
  font-size:12px;
}
.footer .zong p:first-child{
  margin-top:8px;
  margin-bottom:5px;
}
.footer .zong p:first-child span:first-child{
  font-weight:700;
  font-size:16px;
  margin-right:5px;
}
.footer .zong p:first-child span:last-child{
  color:red;
  font-size:15px;
}
.footer .zong p:last-child{
  color:#666;
}
.footer .zong p:last-child span{
  position: relative;
  top:0px;
}
.footer .jie{
  background-color:#e4393c;
  width:100px;
  font-size:18px;
  color:#fff;
  line-height: 60px;
  text-align:center;
  border-radius:2px;
  margin-left:30px;;
}
/*头部样式*/
.mint-header.is-fixed{
  background-color:#fff;
  color: #000;
  font-size: 18px
}
.content .price span:last-child{
  margin-left:68%;
}
/*地址栏修改*/
.dingdan {
  margin-bottom: 5px;
  margin-top: -7px;
}
p{
 color:#666 !important;
  margin-left:2%;
  line-height:14px;
} 
.mui-card-content-inner{
  padding:8px !important;
}
.min-card{
  margin:15px;
}
.mui-card{
  left:4px;
}
</style>



