<template>
<div class="container">
  <mt-header fixed title="地址管理">
    <router-link to="/Me" slot="left">
      <mt-button icon="back"></mt-button>
    </router-link>
  </mt-header>
  <!-- 收货地址 -->
  <!-- 添加住址 -->
  <div class="jia">
    <button @click="add" class="tian">添加住址</button>
  </div>
  <div class="mui-card carr" id="haha" >
    <div class="mui-card-content ">
      <div class="mui-card-content-inner dizhi" id="22" v-for="(item,i) of addrlist" :key=i>
          <p >
            <span>{{item.name}}  </span>
            <span>{{item.tle}}</span>
          </p>
          <p>{{item.place}}</p>
        <div class="update">
          <div @click="update">
              <span class="mui-icon mui-icon-compose"></span>
              <span :data-id="item.id">编辑</span>
          </div>
          <div @click="del">
              <span class="mui-icon mui-icon-trash"></span>
              <span :data-id="item.id">删除</span>
          </div>
        </div>    
      </div>  
    </div>
  </div>
  
  <!-- 底部导航 -->

</div>
</template>
<script>
export default {
  data () {
    return{
      addrlist:[] //收货地址
     
    }
  },
  created(){
    this.addr();
  },
  methods:{
    //收货地址
    addr(){
      var url="addrlist";
      this.axios.get(url).then(result=>{
        //console.log(result.data);
         this.addrlist=result.data.data;
         //console.log(this.addrlist)
      })
    },
    //删除地址
    del(e){
      var id=e.target.dataset.id;
      var url="del?id="+id;
      this.axios.get(url).then(result=>{
        //console.log(result.data);
        if(result.data.code==1){
          this.addr();
        }
      })
    },
    //修改地址
    update(e){
       var id=e.target.dataset.id;
       console.log(id);
       this.$router.push("/update?id="+id);
    },
    //添加地址
    add(){
      this.$router.push("/insert");
    }
  }
}
</script>
<style>
/*底部导航*/


/*页面*/
.container{
  padding-top:40px !important;
}
#haha{
  height:235px;
}
#haha .mui-card-content-inner{
  margin-top: 10px;
}
.carr{
  margin:15px !important;
}
/*编辑 删除*/
.update{
  display: flex;
  flex-direction: row-reverse;
}
.update div{
  padding-left:15px; 
}
/*添加地址*/
.tian{
  margin-top: 10px;
  width: 120px;
  border-radius:5px;
  margin-bottom:10px;
  margin-left: 62%;
}
.jia{
  border-bottom:1px solid #d2d1d3;
}
.dizhi p:nth-child(2){
  padding-bottom: 10px;
  border-bottom: 1px solid #e8e8e8;
}
</style>



