<template>
<div>
 <mt-header fixed title="地址修改">
    <router-link to="/Adress" slot="left">
      <mt-button icon="back"></mt-button>
    </router-link>
  </mt-header>
  <!-- 修改地址 -->
  <div class="addr_box">
    <p class="xing">
      <span>收货姓名 : </span><br>
      <span>
        <input type="text" v-model="name" placeholder="请输入2-7字符汉字姓名">
      </span>
    </p> 
    <p class="dian">
      <span>联系电话 : </span><br>
      <span>
        <input type="text" v-model="tle" placeholder="请输入您的联系电话">
      </span>
    </p>
    <p class="di">
      <span>收货地址 : </span><br>
      <span>
        <input type="text" v-model="place" placeholder="请输入您的详细地址">
      </span>
    </p> 
    <div class="gai">
      <button @click="update">确认修改</button>
    </div>
    
  </div> 

 
</div>
</template>
<script>
import {Toast} from "mint-ui";
export default {
  data(){
    return{
      id:this.$route.query.id,
      addrlist:{}, //住址
      name:'',
      tle:'',
      place:""
    }
  },
  created(){
    this.load();
  },
  methods:{
    //加载当前用户的住址
    load(){
      var url="addr?id="+this.id;
      console.log(this.id);
        this.axios.get(url).then(result=>{
          // console.log(result.data);
          this.addrlist=result.data.data[0];
          console.log(this.addrlist);
          this.name=this.addrlist.name;
          this.tle=this.addrlist.tle;
          this.place=this.addrlist.place;
        })
    },
    update(){
      var name=this.name;
      var tle=this.tle;
      var place=this.place;
      //console.log(name+":"+tle+":"+place);
      var reg1=/^[\u4e00-\u9fa5]{2,7}$/;
      var reg2=/^((\+86|0086)\s+)?1[3-8]\d{9}$/;
      if(!reg1.test(name)){
        Toast("姓名格式不正确,请重新输入");
        return;
      }
      if(!reg2.test(tle)){
        Toast("联系方式格式不正确,请重新输入");
        return;
      }
      var postData=this.qs.stringify({
        id:this.id,
        name:name,
        tle:tle,
        place:place
      })
      //console.log(postData);
      var url="update?id="+this.id;
      //console.log(this.id);
      this.axios.post(url,postData).then(result=>{
        console.log(result.data.code);
        if(result.data.code==1){
          Toast("修改成功");
          this.$router.push("/Adress");
        }
      })
    }
  }
}
</script>
<style>
.addr_box{
  margin-top:45px;
}
.addr_box p{
  margin-bottom:0;
  margin-left:3%;
}
.addr_box p span{
  line-height:30px;
  color:#333;
  font-size: 16px;
}
.addr_box p span:last-child input{
  width:300px;
  margin-left: 8%;
  margin-bottom:5px;
  padding:5px 10px;
}
input::-webkit-input-placeholder {
  color: #aab2bd;
  font-size: 14px;
}
.addr_box .gai button{
  margin-top:5px;
  margin-left:69%;
  color:#fff;
  background-color:#e93b3d;
}
.addr_box .gai {
  margin-top:5px;
  padding-top:5px;
  border-top:1px solid #d2d1d3;
}
</style>



