<template>
  <div>
    <!-- 1:公共顶部导航条 -->
    <!-- <mt-header fixed title="允陌 i 生活馆">
      <mt-button icon="back" slot="left"></mt-button>
      <mt-button icon="more" @click="openNav()" slot="right">
        <div id="side">
          <div id="mySidenav" class="sidenav">
          <span class="closebtn" @click.stop="closeNav()">
            &times;
          </span>
            <router-link to="login">登录</router-link>
            <router-link to="register">注册</router-link>
          </div>
        </div>
      </mt-button>
    </mt-header> -->
    <!-- 容器-->
    <router-view/>
    <mt-tabbar v-model="message" fixed>
     <mt-tab-item id="Home">
      <img slot="icon" src="../img/tabs/index.png">
          首页
     </mt-tab-item>
     <mt-tab-item id="GoodList">
      <img slot="icon" src="../img/tabs/product.png">
          全部商品
     </mt-tab-item>
     <mt-tab-item id="ShopCart">
          <img slot="icon" src="../img/tabs/shop.png">
          购物车
     </mt-tab-item>
     <mt-tab-item id="Me">
      <img slot="icon" src="../img/me.png">
            个人中心
     </mt-tab-item>
    </mt-tabbar>
  </div>
</template>
<script>
  export default {
    data(){
      return{
        //选中的tabbar值message为外面页面传入的值selected
        message:this.selected,
        //这里使用的icon图标为图片，所以需要加图片改变的传入，若使用阿里图标，则不用加
        atabs:this.tabs,
      }
    },
    props:{
      selected: String,
      tabs:Array,
    },
    watch: {
      message: function (val, oldVal) {
        // 这里就可以通过 val 的值变更来确定去向
        switch(val){
          case 'Home':
            this.$router.push('/Home');
            break;
          case 'GoodList':
            this.$router.push('/GoodList');
            break;
          case 'ShopCart':
            this.$router.push('/ShopCart');
            break;
          case 'Me':
            this.$router.push('/Me');
            break;
        }
      }
    },
  }

</script>
<style>
.mint-tab-item-icon > *{
  margin-left:-7px;
}
</style>



