<template>
  <div class="home_box">
    <!-- 1:公共顶部导航条 -->
    <mt-header fixed title="允陌 i 生活馆">
      <mt-button icon="back" slot="left"></mt-button>
      <mt-button icon="more" @click="openNav()" slot="right"> 
      <div id="side">
        <div id="mySidenav" class="sidenav">
          <span class="closebtn" @click.stop="closeNav()">
            &times;
          </span>
          <router-link to="login">登录</router-link>
          <router-link to="register">注册</router-link>
        </div>
      </div>
      </mt-button>
    </mt-header>
    <!-- 搜索框 -->
    <div class="input">
      <ul>
        <li>
          <img src="../img/ss.png" alt="图片加载失败" class="search">
        </li>
        <li>
          <input type="search" class="mui-input-clear" placeholder="搜商品">
        </li>
        <li>精选</li>
        <li>商品</li>
        <li>新品</li>
        <li>买家秀</li>
        <li>动态</li>
      </ul>
    </div>
    <!-- 图片 -->
    <div class="box">
      <div class="img" v-for="item of list" :key="item.id">
        <img :src="item.img">
      </div>
    </div>
    <!-- 底部导航 -->
    <mt-tabbar fixed>
      <mt-tab-item id="tab1" class="home">
        <img slot="icon" src="../img/tabs/index-active.png">
        首页
      </mt-tab-item>
      <router-link to="/GoodList">
        <mt-tab-item id="tab2" class="quanbu">
          <img slot="icon" src="../img/tabs/product.png">
          全部商品
        </mt-tab-item>
      </router-link>
      <router-link to="/ShopCart">
        <mt-tab-item id="tab3"  class="shop">
          <img slot="icon" src="../img/tabs/shop.png">
          购物车
        </mt-tab-item>
      </router-link>
      <router-link to="/Me">
        <mt-tab-item id="tab4"  class="me">
          <img slot="icon" src="../img/me.png">
          个人中心
        </mt-tab-item>
      </router-link>
    </mt-tabbar>
  </div>
</template>
<script>
export default {
  data() {
    return {
      list: [],
      selected:'tab1'
    };
  },
  created() {
    //当组件对象创建成功后即可发送ajax请求
    this.getImages();
  },
  methods: {
    openNav(){
      document.getElementById("mySidenav").style.width = "280px";
      //console.log(1);
    },
    closeNav(){
      var mySidenav=document.getElementById("mySidenav").style=null;
      //console.log(document.getElementById('mySidenav'))
    },
    //获取首页图片
    getImages() {
      //console.log("123");
      var url = "getImages";
      this.axios.get(url).then(result => {
        this.list = result.data;
        //console.log("123");
        //console.log(result.data[0].img);
      });
    }
  }
};
</script>
<style>
.mint-header.is-fixed{
  background-color: #79abb2 !important;
  color: #fff !important;
}
.home_box .mint-header-title {
  font-size: 18px;
}
.home_box .mint-header {
  background-color: #79abb2;
}

.home_box .input ul {
  width: 56vh;
  height: 35px;
  background-color: #fff;
  display: flex;
  justify-content: space-around;
  margin-top: 40px;
  padding: 0px;
  position: absolute;
}
.home_box .input ul li {
  list-style: none;
  line-height: 33px;
  margin-left: -36px;
  font-size:15px;
}
.home_box .input {
  position: fixed;
  top: 0;
}
.app-container {
  padding-top: 0;
  padding-bottom: 0;
}
.home_box div select {
  width: 100px;
}
.home_box input[type="search"] {
  border-radius: 20px;
  border: 1px solid rgb(236, 236, 236);
  width: 90px;
  background: #fff;
  margin-bottom: 0px;
  text-align: right;
  height: 30px;
}
.home_box .input img {
  width: 12px;
  height: 12px;
}
.search {
  position: relative;
  left: 32px;
}
.home_box .mui-bar .mui-title {
  margin: 20px;
  right: 23px;
}
.box {
  margin-top: 76px;
}
.img {
  width: 370px;
  margin: 0 auto;
}
.img img {
  width: 100%;
}
/*底部导航*/
.mint-tab-item-label{
  color:#000  !important;
}

#side{
position: absolute;
top:0;
z-index:20;

}
#mySidenav{
background:url('../img/bg.jpg');
background-size:280px auto;
color:white !important;
}
.sidenav {
  white-space:nowrap;
  height: 100%;
  width: 0;
  position: fixed;
  z-index: 1;
  top: 0;
  left: 0;
  background-color:#f8f9fa;
  overflow-x: hidden !important;
  transition: 0.5s;
  padding-top: 60px;
}
.sidenav a {
  padding: 8px 8px 8px 32px;
  text-decoration: none;
  font-size: 18px;
  color: #333;
  display: block;
  transition: 0.3s;
}
.sidenav a:hover, .offcanvas a:focus{
color:red;
}
.sidenav .closebtn {
  position: absolute ;
  top: 0;
  right: 25px;
  font-size: 36px;
  margin-left: 50px;
  color:#000;
}
#mySidenav img{
  width:50px;
  border-radius:50%
}

.home{
  position: relative;
  left:-70px !important;
}
.quanbu{
  position: relative;
  left:-125px !important;
}
.shop{
  position: relative;
  left:-70px;
}
.me{
  position: relative;
  left:-25px;
}
</style>




